import { Bounce, toast } from 'react-toastify';
import { useSelector } from 'react-redux/es/hooks/useSelector';
import { RootState } from '../../store';
import { useDispatch } from 'react-redux/es/hooks/useDispatch';
import { update } from '../../feature/form';
import { Button, FormBlock, Input, Label, Wrapper } from './Form.styled';
import plusIcon from '../../assets/images/plus.png'


export const Form = (props: { createNewToDo: Function }) => {
  const inputForm = useSelector((state: RootState) => state.inputForm.value); //  
  const dispatch = useDispatch();

  const formSubmit = (e: React.FormEvent) => {
    e.preventDefault(); //   
    if (!inputForm.trim()) {
      //        ,   
      toast.error('  !', {
        position: "bottom-right",
        autoClose: 3000,
        hideProgressBar: false,
        closeOnClick: false,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "dark",
        transition: Bounce,
      });
      return;
    }
    props.createNewToDo(inputForm); //  
    dispatch(update('')); //   
    toast.success(' !', {
      position: "bottom-right",
      autoClose: 3000,
      hideProgressBar: false,
      closeOnClick: false,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
      theme: "dark",
      transition: Bounce,
    });
  };

  return (
    <Wrapper>
      <FormBlock onSubmit={formSubmit}>
        <Label>
          <Input
            value={inputForm}
            type="text"
            onChange={(e) => {
              dispatch(update(e.target.value));
            }}
          />
          <Button icon={plusIcon} />
        </Label>
      </FormBlock>
    </Wrapper >
  );
};