/** @format */

import { configureStore } from "@reduxjs/toolkit";
import { todoSlice } from "./feature/todoList";
import { themeSlice } from "./feature/themeList";
import { inputSlice } from "./feature/form";
import { loadFromLocalStorage, saveToLocalStorage } from "./helpers/storage";

export const store = configureStore({
  reducer: {
    todoList: todoSlice.reducer, //     
    inputForm: inputSlice.reducer, //    
    themeList: themeSlice.reducer,
  },
  preloadedState: loadFromLocalStorage(), //    localStorage  
});

store.subscribe(() => {
  saveToLocalStorage(store.getState());
}); //    localStorage

export type RootState = ReturnType<typeof store.getState>; //     
export type AppDispatch = typeof store.dispatch; //   dispatch
